.form__container {
  width: auto;
  @media (min-width: $tablet-width) {
    padding: 0 40px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
  }
  @media (min-width: $desktop-width) {
    padding: 0;
    column-gap: 80px;
  }
}



.form__group {
  border: none;
  margin: 0; //    ,    .form__group     fieldset        legend                  ,              fieldset
  padding: 0;
}

.form__group--cat {
  border-bottom: 1px solid $special_grey-4;
  padding-bottom: 24px;
}


.form__group--contacts,
.form__group--comment,
.form__group--additional,
.form__footer {
  grid-column: 1 / -1;
}
.form__footer {
  padding: 34px 0 32px;
  text-align: center;
  @media (min-width: $tablet-width) {
    padding: 54px 0 72px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 60px;
    text-align: left;
    align-items: center;
  }
  @media (min-width: $desktop-width) {
    padding: 61px 0 82px;
  }
}

.form__button {
  display: block;
  width: 100%;
  @media (max-width: $mobile-width-only) {
    margin-bottom: 21px;
  }
}

.form__explanation {
  margin: 0;
  font-family: $font-body;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  @media (min-width: $tablet-width) {
    font-size: 16px;
  }
}
