
legend {
  width: 100%;
}

.field-group__heading {
  display: block; //     span    display: inline;        
  margin-bottom: 35px;
  width: 280px;
  margin-right: auto;
  margin-left: auto;
  font-family: $font-heading;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  @media (min-width: $tablet-width) {
    width: max-content;
  }
}

.field-group__list {
  list-style-type: none;
  padding: 0;
  margin: 0 auto;
  width: 280px;
  &--target {
    gap: 13px;
  }

  &--contacts {
    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 80px;
    }
  }
  &--additional{
    @media (min-width: $tablet-width) {
      grid-template-columns: 1fr 1fr; 
      row-gap: 32px;
      column-gap: 60px;
    }
    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(4, 1fr);
      column-gap: 80px;
    }
  }
}

.field-group__item {
  &:not(:last-child) {
    margin-bottom: 13px;
  }
}


.field__lead,
.field__input {
  font-family: $font-heading;
  color: $special-dark-grey;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}

.field__input::placeholder {
  color: $special-dark-grey;
}

